import {
    Directive,
    Input,
    HostBinding,
    HostListener
} from '@angular/core';

import { SelectionService } from './selection.service';

/**
 * @hidden
 */
@Directive({
    selector: '[kendoGridSelectable]'
})
export class SelectableDirective {
    @Input() public index: number;

    private enabled: boolean;
    private ignored: RegExp = /^(a|input|textarea)$/i;

    constructor(private selectionService: SelectionService) {
    }

    @Input()
    public set kendoGridSelectable(enabled: boolean) {
        this.enabled = enabled;
    }

    @HostBinding('class.k-state-selected')
    private get className(): boolean{
        return this.selectionService.isSelected(this.index);
    }

    @HostListener('click', ['$event.target'])
    protected onClick(target: HTMLElement): void {
        if (!this.enabled) {
            return;
        }

        if (this.shouldSelect(target.tagName)) {
            this.selectionService.toggle(this.index);
        }
    }

    protected shouldSelect(tagName: string): boolean {
        return !this.ignored.test(tagName);
    }
}



